import React, { FC } from "react";
import { Svgs } from "../../../constants/images";
//styles
import { Container, Section, Title, Value } from "../cardAndroidDetails.styles";
import { CinemaCardDetailsProps } from "./cinemaCardDetails.types";

const CinemaCardDetails: FC<CinemaCardDetailsProps> = (props) => {
  //props
  const {
    holderName,
    place,
    address,
    openTime,
    startDate,
    endDate,
    ticketNumber,
    termsAndConditions,
    movieName,
    rating,
    googleMapsLink,
    phoneNumber,
    email,
  } = props;
  //render
  return (
    <Container>
      <Section>
        <Title>Ticket holder</Title>
        <Value>{holderName}</Value>
      </Section>
      <Section>
        <Title>{place}</Title>
        <Value>{address}</Value>
      </Section>
      <Section>
        <Title>Doors open</Title>
        <Value>{openTime}</Value>
      </Section>
      <Section>
        <Title>Event start time</Title>
        <Value>{startDate}</Value>
      </Section>
      <Section>
        <Title>Event end time</Title>
        <Value>{endDate}</Value>
      </Section>
      <Section>
        <Title>Ticket number</Title>
        <Value>{ticketNumber}</Value>
      </Section>
      <Section>
        <Title>Terms and conditions</Title>
        <Value>{termsAndConditions}</Value>
      </Section>
      <Section>
        <Title>Movie name</Title>
        <Value>{movieName}</Value>
      </Section>
      <Section>
        <Title>Rating</Title>
        <Value>{rating}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.PositionIcon height="16px" width="16px" />
        <Value>{googleMapsLink}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.PhoneIcon height="16px" width="16px" />
        <Value>{phoneNumber}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.EmailIcon height="16px" width="16px" />
        <Value>{email}</Value>
      </Section>
    </Container>
  );
};

export default CinemaCardDetails;
