import React, { FC } from 'react';
//styles
import {
  HeaderContainer,
  LogoContainer,
  LeftContainer,
  RightContainer,
  HeaderTitle,
  HeaderSubTitle,
  HeaderHeading
} from './header.styles';
//types
import { HeaderProps } from '../../components/index.types';

const Header: FC<HeaderProps> = (props) => {
  //props
  const { headerLogoLink, headerTitle, title, mainTitle } = props;
  //render
  return (
    <HeaderContainer>
      <LeftContainer>
        <LogoContainer src={headerLogoLink} />
        <HeaderTitle>{headerTitle}</HeaderTitle>
      </LeftContainer>
      <RightContainer>
        <HeaderSubTitle color='#446A6B'>{title}</HeaderSubTitle>
        <HeaderHeading>{mainTitle}</HeaderHeading>
      </RightContainer>
    </HeaderContainer>
  );
};

export default Header;
