import React, { FC } from 'react';
//styles
import {
  Container,
  Divider,
  Row,
  Section,
  Title,
  Value
} from '../cardIOSDetails.styles';
//types
import { StoreCardDetailsProps } from './storeCardDetails.types';

const StoreCardDetails: FC<StoreCardDetailsProps> = (props) => {
  const {
    customTitle,
    customTitlePhone,
    description,
    holderLastName,
    holderName,
    kind,
    address,
    schedule,
    accessCodeId,
    offerId,
    couponId,
    phoneNumber,
    phoneNumberSecondary,
    website
  } = props;
  return (
    <Container>
      <Section>
        <Title>{customTitle}</Title>
        <Value>{description}</Value>
      </Section>
      <Divider />
      <Section>
        <Title>{holderName}</Title>
        <Value>{holderLastName}</Value>
      </Section>
      <Divider />
      <Section>
        <Title>type</Title>
        <Value>{kind}</Value>
      </Section>
      <Divider />
      <Section>
        <Title>address</Title>
        <Value>{address}</Value>
      </Section>
      <Divider />
      <Section>
        <Title>schedule</Title>
        <Value>{schedule}</Value>
      </Section>
      <Divider />
      <Section>
        <Title>barcode</Title>
        <Value>{accessCodeId}</Value>
      </Section>
      <Divider />
      <Section>
        <Title>offers</Title>
        <Value>{offerId}</Value>
      </Section>
      <Divider />
      <Section>
        <Title>coupons</Title>
        <Value>{couponId}</Value>
      </Section>
      <Divider />
      <Section>
        <Title>{customTitlePhone}</Title>
        <Row>
          <Value>Commercial phone :</Value>
          <Value color='#3775d1'>{phoneNumber}</Value>
        </Row>
        <Row>
          <Value>After sale service :</Value>
          <Value color='#3775d1'>{phoneNumberSecondary}</Value>
        </Row>
      </Section>
      <Divider />
      <Section>
        <Title>website</Title>
        <Value color='#3775d1'>{website}</Value>
      </Section>
    </Container>
  );
};

export default StoreCardDetails;
