import React, { FC } from "react";
//contants
import { Svgs } from "../../constants/images";
//styles
import {
  StyledDetailsBotton,
  StyledDetailsBottonContainer,
} from "./detailsbuttons.styles";
//types
import { DetailsButtonProps } from "./detailsButton.types";

const DetailsButton: FC<DetailsButtonProps> = (props) => {
  //props
  const { open, setOpen } = props;
  //functions
  const onClick = () => {
    setOpen(!open);
  };
  //render
  return (
    <StyledDetailsBottonContainer>
      <StyledDetailsBotton showDetails={open} onClick={onClick}>
        <Svgs.ExpandArrowIcon />
        <span>details</span>
      </StyledDetailsBotton>
    </StyledDetailsBottonContainer>
  );
};

export default DetailsButton;
