import React, { FC } from 'react';
//styles
import {
  Container,
  Divider,
  Section,
  Title,
  Value
} from '../cardIOSDetails.styles';
//types
import { CouponCardDetailsProps } from './couponCardDetails.types';

const CouponCardDetails: FC<CouponCardDetailsProps> = (props) => {
  const { description, promoCode, couponId } = props;
  return (
    <Container>
      <Section>
        <Title textTransform='none'>Description</Title>
        <Value>{description}</Value>
      </Section>
      <Divider />
      <Section>
        <Title textTransform='none'>Promo Code</Title>
        <Value>{promoCode}</Value>
      </Section>
      <Divider />
      <Section>
        <Title textTransform='none'>Coupon ID</Title>
        <Value>{couponId}</Value>
      </Section>
    </Container>
  );
};

export default CouponCardDetails;
