import React, { FC } from 'react';
//styles
import { Container, Image, ImageContainer, Text } from './access.styles';
//types
import { AccessProps } from '../../components/index.types';

const Access: FC<AccessProps> = (props) => {
  //props
  const { accessImage, accessType } = props;
  //render
  return (
    <Container>
      <ImageContainer type={accessType}>
        <Image src={accessImage} />
      </ImageContainer>
      {props.accessCodeId && <Text>{props.accessCodeId}</Text>}
    </Container>
  );
};

export default Access;
