import React, { FC } from 'react';
//styles
import {
  Container,
  Divider,
  Section,
  Title,
  Value
} from '../cardIOSDetails.styles';
//types
import { PlaneCardDetailsProps } from './planeCardDetails.types';

const PlaneCardDetails: FC<PlaneCardDetailsProps> = (props) => {
  const { description, confirmationCode } = props;
  return (
    <Container>
      <Section>
        <Title>flight details</Title>
        <Value>{description}</Value>
      </Section>
      <Divider />
      <Section>
        <Title>confirmation code</Title>
        <Value>{confirmationCode}</Value>
      </Section>
    </Container>
  );
};

export default PlaneCardDetails;
