import React, { FC } from "react";
import { Svgs } from "../../../constants/images";
//styles
import { Container, Section, Title, Value } from "../cardAndroidDetails.styles";
import { RoomCardDetailsProps } from "./roomCardDetails.types";

const RoomCardDetails: FC<RoomCardDetailsProps> = (props) => {
  //props
  const {
    holderName,
    place,
    address,
    termsAndConditions,
    kind,
    startDate,
    endDate,
    googleMapsLink,
    phoneNumber,
    email,
  } = props;
  //render
  return (
    <Container>
      <Section>
        <Title>Ticket holder</Title>
        <Value>{holderName}</Value>
      </Section>
      <Section>
        <Title>{place}</Title>
        <Value>{address}</Value>
      </Section>
      <Section>
        <Title>Terms and conditions</Title>
        <Value>{termsAndConditions}</Value>
      </Section>
      <Section>
        <Title>Type</Title>
        <Value>{kind}</Value>
      </Section>
      <Section>
        <Title>Check in</Title>
        <Value>{startDate}</Value>
      </Section>
      <Section>
        <Title>Check out</Title>
        <Value>{endDate}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.PositionIcon height="16px" width="16px" />
        <Value>{googleMapsLink}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.PhoneIcon height="16px" width="16px" />
        <Value>{phoneNumber}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.EmailIcon height="16px" width="16px" />
        <Value>{email}</Value>
      </Section>
    </Container>
  );
};

export default RoomCardDetails;
