import React, { FC } from "react";
import { Svgs } from "../../../constants/images";
//styles
import { Container, Section, Title, Value } from "../cardAndroidDetails.styles";
import { Image } from "./busCardDetails.styles";
import { BusCardDetailsProps } from "./busCardDetails.types";

const BusCardDetails: FC<BusCardDetailsProps> = (props) => {
  //props
  const {
    startDate,
    endDate,
    price,
    number,
    ticketImage,
    details,
    googleMapsLink,
    phoneNumber,
    disableNotifications,
  } = props;
  //render
  return (
    <Container>
      <Section>
        <Title>Departure</Title>
        <Value>{startDate}</Value>
      </Section>
      <Section>
        <Title>Arriving at</Title>
        <Value>{endDate}</Value>
      </Section>
      <Section>
        <Title>Price</Title>
        <Value>{price}</Value>
        <Image src={ticketImage} />
      </Section>
      <Section>
        <Title>Number</Title>
        <Value>{number}</Value>
      </Section>
      <Section>
        <Title>Details</Title>
        <Value>{details}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.PositionIcon height="16px" width="16px" />
        <Value>{googleMapsLink}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.PhoneIcon height="16px" width="16px" />
        <Value>{phoneNumber}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.GlobeIcon height="16px" width="16px" />
        <Value>{disableNotifications}</Value>
      </Section>
    </Container>
  );
};

export default BusCardDetails;
