import React, { FC } from "react";
import { Svgs } from "../../../constants/images";
//styles
import {
  Container,
  Row,
  Section,
  Title,
  Value,
} from "../cardAndroidDetails.styles";
import { StoreCardDetailsProps } from "./storeCardDetails.types";

const StoreCardDetails: FC<StoreCardDetailsProps> = (props) => {
  //props
  const {
    holderName,
    holderId,
    kind,
    date,
    address,
    customTitle,
    customValue,
    endDate,
    startDate,
    description,
    phoneNumber,
    phoneNumberSecondary,
    website,
    disableNotifications,
  } = props;
  //render
  return (
    <Container>
      <Section>
        <Title>Client name</Title>
        <Value>{holderName}</Value>
      </Section>

      <Section>
        <Title>Member ID</Title>
        <Value>{holderId}</Value>
      </Section>

      <Section>
        <Title>Type</Title>
        <Value>{kind}</Value>
      </Section>

      <Section>
        <Title>Date</Title>
        <Value>{date}</Value>
      </Section>

      <Section>
        <Title>Address</Title>
        <Value>{address}</Value>
      </Section>

      <Section>
        <Title>{customTitle}</Title>
        <Value>{customValue}</Value>
      </Section>

      <Section>
        <Title>Schedule</Title>
        <Row>
          {startDate && <Value>{startDate}</Value>}
          {endDate && <Value>-</Value>}
          {endDate && <Value>{endDate}</Value>}
        </Row>
      </Section>

      <Section>
        <Title>Offer description</Title>
        <Value>{description}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.PhoneIcon height="16px" width="16px" />
        <Title>Commercial phone</Title>
        <Value>{phoneNumber}</Value>
      </Section>

      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.PhoneIcon height="16px" width="16px" />
        <Title>After sale service</Title>
        <Value>{phoneNumberSecondary}</Value>
      </Section>

      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.GlobeIcon height="16px" width="16px" />
        <Value>{website}</Value>
      </Section>

      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.GlobeIcon height="16px" width="16px" />
        <Value>{disableNotifications}</Value>
      </Section>
    </Container>
  );
};

export default StoreCardDetails;
