import React, { FC } from "react";
import { Svgs } from "../../../constants/images";
//styles
import { Container, Section, Title, Value } from "../cardAndroidDetails.styles";
import { EventCardDetailsProps } from "./eventCardDetails.types";

const EventCardDetails: FC<EventCardDetailsProps> = (props) => {
  //props
  const {
    holderName,
    place,
    address,
    openTime,
    startDate,
    ticketNumber,
    value,
    termsAndConditions,
    googleMapsLink,
    phoneNumber,
    email,
    disableNotifications,
  } = props;
  //render
  return (
    <Container>
      <Section>
        <Title>Ticket holder</Title>
        <Value>{holderName}</Value>
      </Section>
      <Section>
        <Title>{place}</Title>
        <Value>{address}</Value>
      </Section>
      <Section>
        <Title>Date</Title>
        <Value>{openTime}</Value>
      </Section>
      <Section>
        <Title>Start date</Title>
        <Value>{startDate}</Value>
      </Section>
      <Section>
        <Title>Value</Title>
        <Value>{startDate}</Value>
      </Section>
      <Section>
        <Title>Ticket number</Title>
        <Value>{ticketNumber}</Value>
      </Section>
      <Section>
        <Title>Value</Title>
        <Value>{value}</Value>
      </Section>
      <Section>
        <Title>Terms and conditions</Title>
        <Value>{termsAndConditions}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.PositionIcon height="16px" width="16px" />
        <Value>{googleMapsLink}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.PhoneIcon height="16px" width="16px" />
        <Value>{phoneNumber}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.EmailIcon height="16px" width="16px" />
        <Value>{email}</Value>
      </Section>
      <Section flexDirection="row" alignItems="center" gap="12px">
        <Svgs.GlobeIcon height="16px" width="16px" />
        <Value>{disableNotifications}</Value>
      </Section>
    </Container>
  );
};

export default EventCardDetails;
