import React, { FC } from 'react';
//styles
import { ImageContainer, Container, Image, AccessCode } from './access.styles';
//types
import { AccessProps } from '../../components/index.types';

const Access: FC<AccessProps> = (props) => {
  const { accessType, accessImage, accessCodeId } = props;
  return (
    <Container type={accessType}>
      <ImageContainer>
        <Image src={accessImage} type={accessType} alt={accessType} />
        {accessCodeId && (
          <AccessCode type={accessType}>{accessCodeId}</AccessCode>
        )}
      </ImageContainer>
    </Container>
  );
};

export default Access;
